#header {
    display: flex;
    flex-direction: row;
    align-items: center;
    position: relative;
    justify-content: space-between;
    text-align: center;
    background-color: rgba(11, 12, 16, 0.85);
    color: #63F2D8;
    margin-top: -10px;
    border-bottom: 2px solid #49A69C;
    /* box-shadow: 3px 3px 10px 2px #0B0C10; */

}

#title {
    font-family: "Racing Sans One", sans-serif;
    text-shadow: 0 0 0px #2ee, 0 0 0px #2ee, 0 0 10px #00ffff;
    letter-spacing: 2px;
    font-weight: 800;
    margin-top: 0%;
}

.menu-btn {
    display: none;
}

.nav-menu {
    position: fixed;
    top: 0;
    right: -250px;
    width: 250px;
    height: 100%;
    display: flex;
    flex-direction: column;
    padding: 20px;

}

.top {
    flex: 1;
    margin: 20px;
}

#header img {
    width: 80%;
    margin: -23%;
    margin-top: -23%;
    padding-left: 0px;
}
