.searchHome{
    background: #F6F5F3;
    color: #0B0C10;
    border: 2px solid #49A69C;
    padding:5px;
    padding-right:50%;
}
#sec-playlists2 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
    margin: 40px;
    margin-top: 20px;
    background-color: rgba(11, 12, 16, 0.85);

}

#sec-playlists2 a:hover {
    color: #296f68;
}

.profile-playlist {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 2px solid #49A69C;
    box-shadow: 1px 1px 12px #49A69C;
    padding: 5px;
    text-decoration: none;
    color: #63F2D8;
}

.profile-playlist p {
    margin: 0;
    margin-top: 10px;
    margin-bottom: 20px;
}

.profile-playlist h1 {
    margin: 0;
}

.profile-playlist img {
    width: 50%;
    margin-bottom: 10px;
}

#home {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin-top: 0px;
    height: 65%;
}

#home h3 {
    font-size: 40px;
    margin-bottom: 10px;
}

.add a {
    position: absolute;
    top: 20px;
    left: 10px;
    text-decoration: none;
}

.add {
    color: #49A69C;
    text-decoration: none;
}

.add:hover {
    color: #296f68;
}

.extra {
    background-color: rgba(11, 12, 16, 0.85);
    border: 4px solid #0B0C10;
    box-shadow: 3px 3px 10px 6px #0B0C10;
    color: #63F2D8;
    text-align: center;
    margin: 10px;
    padding-bottom: 400px;
}

#friends {
    flex: 1;
    margin-top: 0;
}

#main-playlist {
    flex: 6;
    margin: 10px;
    /* margin-left: -10px; */
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 0;

}
a{
    text-decoration: none;
}
@media (max-width: 768px){
   #sec-playlists2 {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        /* gap:30px;
    margin:40px;
    margin-top:20px;
    background-color: rgba(11, 12, 16, 0.85); */

    }
    .searchHome{
        padding:2px;
        padding-right:30%;
        margin-left:-10px;
        
    }
    #friends h3 {
        font-size: 25px;
    }

    #main-playlist h3 {
        font-size: 25px;
    }

    #friends {
        flex: 1;
    }

    #main-playlist {
        flex: 4;
        margin: 10px;
        /* margin-left: -10px; */
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 0 auto;

    } 
}